.App {
  text-align: center;
  background-color: aqua;
  height:0vh
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Select{
  margin-top: 1%;
  border-style: double;
  border-width: 5px;
}
.Char{
  height:100vh;
  background-color: #61dafb;
}
.backbutton{
  margin-left: 3%;
  width: 3%;
  height: auto;
}
 .back{
   margin-left:-100%; 
   width: 10%;
   height: 20%; 
   background-color: aquamarine;
 }
 .histogram{
   display: flex;
   align-items: flex-end;
   height: 50%;
   margin-top: 0%;
   justify-content: center;
 }
 .histobox{
   width: 5%;
   margin-left: 3%;
   display: block;
 }
 .backb{
   width: 100%;
 }
 .Nav{
   align-items: center;
   background-color: #61dafb;
 }
 .Navs{
   background-color: rgb(125, 187, 238);
   display: flex;
   align-items: center;
   height: 10%
 }
 .LoginLink{
   margin-left: 40%;
 }
 .NavHome{
   margin-left: 1.5%;
   margin-right: 42.7%;
 }
 .NavText{
   text-align: center;
 }
 .VoteLink{
   margin-top: 2%;
   text-align: center;
 }

 .AVG{
   margin-left: 2%;
 }

 .charName{
   width: 100%;
   height: 30%;
   text-align: center;
   display:block;
 }
 .PorBox{
   height: 80%;
 }
 .Portrait{
   justify-content: center;
   height: 80%;
   width:auto;
 }


 .form label{
  display: block;
  text-align: center;
  font-size: 20px;
  margin: 0 0 2vh 0;

}

.LoginDiv{
  justify-content: center;
  height: 100vh;
  background-color: antiquewhite;
  position: relative;
}

.FormDiv{
  border: dashed #38aabe 1vw;
  background-color: rgb(115, 226, 115);
  width:50%;
  margin: auto;
  position: absolute;
  margin-top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%);
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
